.style_btn__1zvbG {
    text-align: center;
    cursor: pointer;
    position: relative;
    background-color: transparent;
    border: 3px solid ghostwhite;
    color: ghostwhite;
    padding: 10px 30px;
    transition-duration: 0.4s;
    overflow: hidden;
}

.style_btn__1zvbG:hover {
    background: ghostwhite;
    color: black;
}

.style_btn__1zvbG:after {
    content: "";
    background: black;
    display: block;
    position: absolute;
    padding-top: 300%;
    padding-left: 350%;
    margin-left: -30px !important;
    margin-top: -120%;
    opacity: 0;
    transition: all 0.8s
}

.style_btn__1zvbG:active:after {
    padding: 0;
    margin: 0;
    opacity: 1;
    transition: 0s
}

.style_btn__1zvbG:focus { outline:0; }
